<template>
  <v-dialog width="750" v-model="dialog">
    <v-card>
      <v-card-title>
        <span class="text-h5 font-weight-bold"> Заказ №{{ order.id }} </span>
      </v-card-title>

      <v-card-text class="mx-0 my-0">
        <v-container :fluid="true">
          <v-row>
            <v-col cols="12">
              <h2 class="font-weight-bold h1 mb-3">
                Реквизиты заказа
              </h2>
              <v-row no-gutters>
                <v-col>Клиент:</v-col>
                <v-col>{{ order.user.information.first_name }}</v-col>
              </v-row>
            </v-col>
            <v-col cols="12">
              <v-row no-gutters>
                <v-col>Адрес:</v-col>
                <v-col>{{ order.data.address }}</v-col>
              </v-row>
            </v-col>
            <v-col cols="12">
              <v-row no-gutters>
                <v-col>Моб. телефон:</v-col>
                <v-col>{{ order.data.phone }}</v-col>
              </v-row>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <h2 class="font-weight-bold h1 mb-3">
                Статус заказа
              </h2>
            </v-col>
            <v-col cols="12">
              <v-select
                v-model="form.status"
                :items="normalizeStatusesForSelect"
                item-text="state"
                return-object
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <h2 class="font-weight-bold h1 mb-3">
                Состав заказа
              </h2>
            </v-col>
            <v-col cols="12">
              <v-simple-table>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">
                        Название товара
                      </th>
                      <th class="text-left">
                        Цена
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="product in orderProducts" :key="product.name">
                      <td>{{ product.name }}</td>
                      <td>{{ product.price }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <v-checkbox
                v-model="notify"
                label="Уведомить клиента об изменении"
              />
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary">
          Обновить заказ
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "UpdateOrderDialog",
  data() {
    return {
      dialog: false,
      notify: false,
      form: {
        status: null
      }
    };
  },
  computed: {
    normalizeStatusesForSelect() {
      return this.statuses.map(status => ({
        state: status.name,
        id: status.id
      }));
    },
    orderProducts() {
      return this.order.products;
    }
  },
  props: {
    order: {
      type: Object,
      required: true
    },
    statuses: {
      type: Array,
      required: true
    }
  },
  watch: {
    order() {
      this.dialog = true;
      const { status } = this.order;
      this.form.status = { state: status.name, id: status.id };
    }
  }
};
</script>

<style scoped></style>
